.profile {
  display: inline-block;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
}

.stats {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 10px 50px;
}

.item {
  display: flex;
  flex-direction: column;
}
